.roadmap {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;

}

.roadback {
    width: 100%;
    height: auto;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    top: 0%;
    background: transparent;

}

.headerr {
    background: transparent;
    z-index: 1;
    position: relative;
    top: 0%;

}

.gradient {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    z-index: 1;
    background: linear-gradient(180deg, #000000 40.2%, rgba(1, 1, 1, 0) 100%);
}

.headerr h1 {
    font-size: 100px;
    font-weight: bold;
    color: #009a0d;
    text-align: center;
    margin-top: 20px;
    background: transparent;
    background-color: transparent;
    color: black;
    -webkit-text-stroke: 1px rgba(53, 216, 36, 1);
    z-index: 2;
}

.roadmap-items {
    position: relative;
    top: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 2rem;
    background: transparent;
    z-index: 1;

}

.roadmap-item {
    width: 20%;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 10px;
    background: transparent;

}

.roadmap-item h3 {
    font-size: 94px;
    font-weight: 600;
    color: #ffffff;
    font-weight: 400;
    line-height: 29.13px;
    text-align: center;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    background: transparent;


}

.roadmap-item img {
    width: 60%;
    height: auto;
    border-radius: 4px;
    position: relative;
    left: 25%;
    background: transparent;

}

.roadmap-item p {
    font-size: 16px;
    color: #555;
    background: transparent;

}

.roadmap-item ul {
    position: relative;
    bottom: 10%;
    padding: 0;
    margin: 0;
    text-align: left;
    background: transparent;


}

.roadmap-item ul li {
    font-family: 'Macondo';
    font-size: 37px;
    color: #ffffff;
    margin-bottom: 5px;
    background: transparent;

}


@media (min-width: 1067px) and (max-width: 1820px) and (min-height: 500px) and (max-height: 1000px) {
   
    .roadmap-item ul li {
        font-size: 16px;

    }

    .roadmap-item img {
        position: relative;
        width: 50%;
        left: 25%;
    }

    .roadmap-item h3 {
        font-size: 30px;
    }

    .roadmap-item {
        position: relative;
        top: 1%;
        width: 25%;
    }

    .gradient {
        background: linear-gradient(180deg, #000000 10.2%, rgba(1, 1, 1, 0) 100%);
        z-index: 0;
    }

    .roadback {
        height: 150%;
    }

    .headerr h1 {
        font-size: 50px;
        z-index: 1;
    }

    .headerr {
        position: relative;
        top: 0%;
        font-size: 50px;
        z-index: 1;
    }
}
